import { createRouter,createWebHashHistory } from "vue-router";
const routes = [
    {   path: '/',
        component: () => import('@/pages/index'),
        redirect:'/home',
        children:[
            {
                path:'/home',
                component: () => import('@/pages/home/index'),
                meta:{index:1}
            },
            {
                path:'/clap',
                component: () => import('@/pages/clap/index')
            },
            {
                path:'/report',
                component: () => import('@/pages/report/index')
            },
            {
                path:'/mine',
                component: () => import('@/pages/mine/index'),
                meta:{index:0}
            }
        ]
    },
    {
        path: '/unsolvedetail',
        component: () => import('@/pages/home/unSolveDetail'),
        meta:{index:2}
    },
    {
        path: '/solvedetail',
        component: () => import('@/pages/home/solveDetail'),
        meta:{index:2}
    },
    {
        path: '/issueCallback',
        component: () => import('@/pages/mine/issueCallback'),
        meta:{index:1}
    },
]
const router = createRouter({
    // 4. 内部提供了 history 模式的实现。为了简单起见，我们在这里使用 hash 模式。
    history: createWebHashHistory(),
    routes,
})
export default  router